import React, { Fragment } from 'react'
import moment from 'moment';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { _proceed_order, _complete_order, _cancel_order, _confirm_order } from '../../../store/actions/serviceAction';

const OrderManagementSummary = ({_proceed_order, _complete_order, _cancel_order, _confirm_order, chk, purchaseList}) => {
  const proceed_order = (e, purchaseList_id) => {
    if(window.confirm('   ?     ,     !')) {
      _proceed_order(purchaseList_id);
    } else {
      return;
    }
  }
  const complete_order = (purchaseList_id) => {
    if(window.confirm('   ?       !    ,  !')) {
      _complete_order(purchaseList_id);
    } else {
      return;
    }
  }
  const cancel_order = (purchaseList_id) => {
    if(window.confirm('   ?      .')) {
      _cancel_order(purchaseList_id);
    } else {
      return;
    }
  }
  const confirm_order = (purchaseList_id) => {
    if(window.confirm(' ?')) {
      _confirm_order(purchaseList_id)
    } else {
      return;
    }
  }

  const disabled_proceed_btn = () => {
    if(purchaseList.cancel) return 'disabled';
    else if(purchaseList.hasOwnProperty('proceed')) return 'disabled';
    else return '';
  }
  const disabled_complete_btn = () => {
    if(purchaseList.cancel) return 'disabled';
    else if(purchaseList.proceed) return '';
    else return 'disabled'; 
  }
  const disabled_review_btn = () => {
    if(purchaseList.cancel) return 'disabled';
    else if(purchaseList.review === true) return 'disabled';
    else if(purchaseList.proceed === false) return '';
    else return 'disabled';
  }
  const disabled_cancle_btn = () => {
    if(purchaseList.cancel || (purchaseList.proceed === false)) return 'disabled';
  }
  
  const isPaid = purchaseList.isPaid;
  let proceed_class = disabled_proceed_btn();
  let complete_class = disabled_complete_btn();
  let cancel_class = disabled_cancle_btn();
  let review_class = disabled_review_btn();
  console.log(purchaseList.proceed);
  return (
    <div className="collection notEmpty" key={purchaseList.date}>
    {
      (!isPaid)
        ? (
          <Fragment>
          <div className="collection-item not-paid row">
            <div className="image-area col s4">
              <img src={purchaseList.imgURL} alt="  " />
            </div>
            <div className="options-area col s8">
              <h6 className='scorehvy'>{ purchaseList.service_title }</h6>
              <p> : { purchaseList.provider_nickName }</p> 
              <p> : { moment(purchaseList.purchasedAt.toDate()).format('YYYY / MM / DD HH:mm') }</p>
            </div>
          </div>
          <div className="warning-Message">
            <h5 className="scorehvy">   .    .</h5>
          </div>
          </Fragment>
        )
        : (
          <Fragment>
          <div className="collection-item paid row">
            <div className="image-area col s4">
              <img src={purchaseList.imgURL} alt="  " />
            </div>
            <div className="options-area col s8">
              <h6 className='scorehvy'>{ purchaseList.service_title }</h6>
              { (chk) ? (<p> : { purchaseList.provider_nickName }</p>) : (<p> : { purchaseList.buyer_nickName }</p>) }
              <p> : { moment(purchaseList.purchasedAt.toDate()).format('YYYY / MM / DD HH:mm') }</p>
              
              <table className='col s12 centered'>
                <thead className='grey lighten-3'>
                  <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td>{ purchaseList.cancel ? <font color='gray'></font> : purchaseList.proceed ? <font color='blue'></font> : (purchaseList.proceed === false) ? <font color='blue'></font> : <font color='red'></font> }</td>
                    <td>{ purchaseList.cancel ? <font color='gray'></font> : purchaseList.requestData ? <font color='blue'></font> : <font color='red'></font> }</td>
                    <td>{ purchaseList.cancel ? <font color='gray'></font> : purchaseList.review ? <font color='blue'></font> : <font color='red'></font> }</td>
                  </tr>
                </tbody>
              </table>
              
            </div>
          </div>

          <div className="collection-item row order-status">
            { (purchaseList.proceed === false) ? <div className="box blue white-text"></div> : null }
            { (purchaseList.hasOwnProperty('request') !== true) ? <div className="box orange white-text"></div> : null }
            { (purchaseList.proceed === true) ? <div className="box myomColor-background white-text"></div> : null }  
            { (purchaseList.review === false) ? <div className="box red white-text"></div> : null }
            { (purchaseList.review === true) ? <div className="box white black-text"></div> : null }
            { (purchaseList.cancel) ? <div className="box grey white-text"></div> : null }     
          </div>
          
          {
            (chk)
              ? (
                <div className="hidden-buttons row">
                  <Link to={purchaseList.requestData ? '/chatDashboard' : '/requestForm/' + purchaseList.id} className="col s2 btn waves-effect myomColor-background"></Link>
                  <Link to={'/purchaseDetails/' + purchaseList.id} className="col s2 btn waves-effect myomColor-background"></Link>                            
                  <div onClick={() => confirm_order(purchaseList.id)} className={"col s2 btn waves-effect myomColor-background " + review_class}></div>
                  <div onClick={() => cancel_order(purchaseList.id)} className={"col s2 btn waves-effect myomColor-background " + cancel_class}></div>
                </div>
              ) 
              : (
                <div className="hidden-buttons row">
                  <div onClick={(e) => proceed_order(e, purchaseList.id)} className={"col s2 btn waves-effect myomColor-background " + proceed_class}></div>
                  <Link to={'/purchaseDetails/' + purchaseList.id} className={"col s2 btn waves-effect myomColor-background "}></Link>
                  <div onClick={() => complete_order(purchaseList.id)} className={"col s2 btn waves-effect myomColor-background " + complete_class}></div>
                  <div onClick={() => cancel_order(purchaseList.id)} className={"col s2 btn waves-effect myomColor-background " + cancel_class}></div>
                </div>
              )
          }
          </Fragment>
        )
    }


      

      
      
    </div>
  )
}
const mapDispatchToProps = (dispatch) => {
  return {
    _proceed_order: (purchaseList_id) => dispatch(_proceed_order(purchaseList_id)),
    _complete_order: (purchaseList_id) => dispatch(_complete_order(purchaseList_id)),
    _cancel_order: (purchaseList_id) => dispatch(_cancel_order(purchaseList_id)),
    _confirm_order: (purchaseList_id) => dispatch(_confirm_order(purchaseList_id)),
  }
}
export default connect(null, mapDispatchToProps)(OrderManagementSummary);