import React, { Component, Fragment } from 'react'
import { Notification, condition, privacy } from './terms';
import M from 'materialize-css';

class CustomerActivator extends Component {
  componentDidMount() {
    M.AutoInit();
  }
  render() {
    const category = this.props.category;
    return (
      <Fragment>
      { 
        (category === 'notifications')
          ? (
            <ul className="collapsible">
              <li>
                <div className="collapsible-header"><i className="material-icons">notifications</i>[] MYOM     (19.10.17)</div>
                <div className="collapsible-body"><pre>{ Notification }</pre></div>
              </li>
            </ul>
          )
          : (category === 'question-answer')
            ? (
            <ul className="collapsible">
              <li>
                <div className="collapsible-header"><i className="material-icons">question_answer</i>     ?</div>
                <div className="collapsible-body">   []  .      11  .</div>
              </li>
              <li>
                <div className="collapsible-header"><i className="material-icons">question_answer</i>     ?</div>
                <div className="collapsible-body">   ,           .     Time Stamp       ,     ()      .</div>
              </li>
              <li>
                <div className="collapsible-header"><i className="material-icons">question_answer</i>      ?</div>
                <div className="collapsible-body">
                      2 .<br/><br/>
                  1)       <br/>
                        ,    Time Stamp     50%   .           .
                  <br/><br/>
                  2)         <br/>
                            .      ,      , 100%                 .
                </div>
              </li>
              <li>
                <div className="collapsible-header"><i className="material-icons">question_answer</i>  ?</div>
                <div className="collapsible-body"> () 15%.</div>
              </li>
              <li>
                <div className="collapsible-header"><i className="material-icons">question_answer</i>     ?</div>
                <div className="collapsible-body">    7  ,       [] .      8  11 .</div>
              </li>
              <li>
                <div className="collapsible-header"><i className="material-icons">question_answer</i>         ?</div>
                <div className="collapsible-body">          ,             .</div>
              </li>
              <li>
                <div className="collapsible-header"><i className="material-icons">question_answer</i>   ?</div>
                <div className="collapsible-body">       .       ,          .                .</div>
              </li>
              <li>
                <div className="collapsible-header"><i className="material-icons">question_answer</i>   ?</div>
                <div className="collapsible-body"> []          .</div>
              </li>
            </ul>
          )
          : (
            <ul className="collapsible">
              <li>
                <div className="collapsible-header"><i className="material-icons">description</i>  (19.10.17)</div>
                <pre className="collapsible-body">{condition}</pre>
              </li>
              
              <li>
                <div className="collapsible-header"><i className="material-icons">description</i>  (19.10.17)</div>
                <pre className="collapsible-body"></pre>
              </li>
              
              <li>
                <div className="collapsible-header"><i className="material-icons">description</i> (19.10.17)</div>
                <pre className="collapsible-body">{privacy}</pre>
              </li>
            </ul>
          )
      }
      </Fragment>
    )
  }
}

export default CustomerActivator;