import React, { Component } from 'react';
import './requestForm.css'
import RequestStep1 from './RequestStep1'
import RequestStep2 from './RequestStep2'
import RequestStep3 from './RequestStep3'
import RequestStep4 from './RequestStep4'
import { sendRequest } from '../../store/actions/requestFormAction';
import { connect } from 'react-redux'

class RequestForm extends Component {
	state = {
		currentStep: 1,
		tripLocation: '',
		editStyle: [
			{id: 1, value: '', isChecked: false},
			{id: 2, value: '', isChecked: false},
			{id: 3, value: '', isChecked: false},
			{id: 4, value: '', isChecked: false},
			{id: 5, value: '', isChecked: false},
			{id: 6, value: ' :', isChecked: false},
		],
		editFeeling: [
			{id: 1, value: '', isChecked: false},
			{id: 2, value: '', isChecked: false},
			{id: 3, value: '', isChecked: false},
			{id: 4, value: '', isChecked: false},
			{id: 5, value: '', isChecked: false},
			{id: 6, value: '', isChecked: false},
			{id: 7, value: '', isChecked: false},
			{id: 8, value: 'B', isChecked: false},
			{id: 9, value: '', isChecked: false},
		],
		hasReference: [
			{id: 1, value: '  ', isChecked: false},
			{id: 2, value: '  ', isChecked: false},
		],
		referenceLink: '',
		purpose: [
			{id: 1, value: '', isChecked: false},
			{id: 2, value: 'SNS ', isChecked: false},
			{id: 3, value: '', isChecked: false},
			{id: 4, value: ' : ', isChecked: false},
		],
		musicFree: [
			{id: 1, value: '', isChecked: false},
			{id: 2, value: '', isChecked: false},
		],
		// whatKindOfMusic: [
		// 	{id: 1, value: '', isChecked: false},
		// 	{id: 2, value: '', isChecked: false},
		// 	{id: 3, value: '', isChecked: false},
		// 	{id: 4, value: '', isChecked: false},
		// ],
		wantMusic: '',
		// editTechnique: [
		// 	{id: 1, value: '', isChecked: false},
		// 	{id: 2, value: ' ', isChecked: false},
		// 	{id: 3, value: ' ', isChecked: false},
		// 	{id: 4, value: '', isChecked: false},
		// 	{id: 5, value: '3D', isChecked: false},
		// ],
		subtitle: [
			{id: 1, value: ' .', isChecked: false},
			{id: 2, value: ' ,  ', isChecked: false},
			{id: 3, value: '   .', isChecked: false},
			{id: 4, value: '     .', isChecked: false},
			{id: 5, value: '', isChecked: false},
		],
		subtitle_font: '',
		essential_cut: [
			{id: 1, value: '', isChecked: false},
			{id: 2, value: '', isChecked: false},
		],
		edit_order: [
			{id: 1, value: ' ', isChecked: false},
			{id: 2, value: ' ', isChecked: false},
			{id: 3, value: ' .', isChecked: false},
		],
		etc_requests: '',
	}
	handleChange = (e) => {
		this.setState({
			[e.target.id] : e.target.value
		})
	}

	handleSubmit = (e) => {
		e.preventDefault();
	}
	

	handleCheck = (e) => {
		let chkElementName = e.target.closest('div').id;
		let checkElements = this.state[chkElementName];
		checkElements.forEach(chkElement => {
			if(chkElement.value === e.target.value)
				chkElement.isChecked = e.target.checked
		})
		this.setState({
			chkElementName : checkElements
		})
	}

	_submit = () => {
		this.props.sendRequest(this.state, this.props.history, this.props.match.params.id);
	}
	_next = () => {
		const required_all = [...document.querySelectorAll('._required')];
		let current_required = required_all.filter(item => !(this.state[item.id]));
		if(current_required.length){
			this.setState({
				need: true,
			})
			return;
		}

		let currentStep = this.state.currentStep;
		currentStep = currentStep >= 3? 4 : currentStep +1 ;
		this.setState({
			currentStep,
			need: false,
		})
	}

	_prev = () => {
		let currentStep = this.state.currentStep;
		currentStep = currentStep <= 1? 1: currentStep -1;
		this.setState({
			currentStep,
		})
	}

	render(){ 
		return(
			<div className="requestForm container" >
				<div className="card">
					<div className="card card-image">
						<img src="/img/purple.jpg"/>
						<span className="card-title white-text">  ({this.state.currentStep}/4)</span>
					</div>
					<form onSubmit={this.handleSubmit}>
						<RequestStep1
							currentStep = {this.state.currentStep}
							need = {this.state.need}
							tripLocation = {this.state.tripLocation}
							handleChange = {this.handleChange}/>

						<RequestStep2
							currentStep = {this.state.currentStep}
							need={this.state.need}
							handleChange = {this.handleChange}
							editStyle = {this.state.editStyle} 
							editFeeling = {this.state.editFeeling}
							handleCheck = {this.handleCheck}/>

						<RequestStep3
							currentStep = {this.state.currentStep}
							need={this.state.need}
							handleChange = {this.handleChange}
							handleCheck = {this.handleCheck}
							hasReference = {this.state.hasReference}
							referenceLink = {this.state.referenceLink} 
							purpose = {this.state.purpose} />		

						{/* <RequestStep4
							currentStep = {this.state.currentStep}
							need={this.state.need}
							handleChange = {this.handleChange}
							handleCheck = {this.handleCheck}
							editTechnique = {this.state.editTechnique} />		 */}

						<RequestStep4
							currentStep = {this.state.currentStep}
							need={this.state.need}
							handleChange = {this.handleChange}
							handleCheck = {this.handleCheck}
							musicFree = {this.state.musicFree}
							wantMusic = {this.state.wantMusic}
							subtitle = {this.state.subtitle}
							subtitle_font = {this.state.subtitle_font} 
							essential_cut = { this.state.essential_cut }
							edit_order = {this.state.edit_order}
							etc_requests = {this.state.etc_requests} />											
						
						<div className="card-action">
							<div className="request-buttons">
								{this.state.currentStep < 4 ? <button onClick={this._next} className="btn myomColor-background right"></button> : null }
								{ this.state.currentStep !== 1 ? <button onClick={this._prev} className="btn myomColor-background left"></button> : null }
								{ this.state.currentStep == 4 &&
									<button onClick = {this._submit} className="btn myomColor-background right"></button>}
							</div>
						</div>
					</form>
				</div>
			</div>
		)
	}
}

const mapDispatchToProps = (dispatch) => {
	return{
		sendRequest: (requestData, history, purchased_id) => dispatch(sendRequest(requestData , history, purchased_id))
	}
}

export default connect(null,mapDispatchToProps)(RequestForm);