import React from 'react'

const ProviderRegisterStep2 = (props) => {
  if(props.currentStep !== 2) return null;

  return (
    <div className="row ProviderRegisterStep2">
      <h5 className="left col s10 offset-s1 scorehvy">  </h5>
      <p className="col s10 offset-s1 editor-history">
                 .<br/>
              !<br/>
        PDF    ,         .<br/>
             ,   .<br/>
              !
      </p>

      <div className="input-field editHistory col s10 offset-s1">
        <table className='responsive-table'>
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>

          <tbody>
            <tr className='grey-text'>
              <td style={{minWidth: '200px'}}>)<br/> 2018.10.07 - 2018.11.07</td>
              <td style={{minWidth: '200px'}}>)<br/>      </td>
              <td style={{minWidth: '200px'}}>)<br/> https://www.youtube.com/watch?v=z8jniusR9iQ</td>
            </tr>
            {
              (props.histories.length && props.histories.map((history, idx) => {
                let name = 'history' + idx;
                let default_taget = props.histories[idx][name];
                return (
                <tr key={name}>
                  <td>
                    <div className="input-field dateSetting">
                      <input onSelect={props.handleDatepicker} id={name + '_date'} value={default_taget[name + '_date'][0]} type="text" className='datepicker startAt' placeholder='~'/>
                      <span>-</span>
                      <input onSelect={props.handleDatepicker} id={name + '_date'} value={default_taget[name + '_date'][1]} type="text" className='datepicker stopAt' placeholder='~'/>
                    </div>
                  </td>
                  <td>
                    <div className="input-field">
                      <textarea onChange={props.handleEditorHistory} id={name + '_content'} value={default_taget[name + '_content']} type="text" className='materialize-textarea' placeholder='   '/>
                    </div>
                  </td>
                  <td>
                    <div className="input-field">
                      <textarea onChange={props.handleEditorHistory} id={name + '_reference'} value={default_taget[name + '_reference']} type="text" className='materialize-textarea' placeholder='   '/>
                    </div>
                  </td>
                </tr>
                )
              }))
            }

          </tbody>
        </table>

        <div id='history' onClick={(e) => props.handleMoreBtn(e)} className="moreBtn col s4 offset-s4 btn myomColor-background waves-effect">+  </div>
      </div>

      <h5 className="left col s10 offset-s1 scorehvy"></h5>
      <p className="col s10 offset-s1 portfolio">
             ,           . <br />
         : untilburnout@naver.com
      </p>

    </div>
  )
}

export default ProviderRegisterStep2;