import React, { Fragment } from 'react'
import Slider from '@material-ui/core/Slider'

const CreateServiceStep4 = (props) => {
  if(props.currentStep !== 4) return null;

  const noWrite = (e) => {
    e.preventDefault();
  }

  return (
    <Fragment>
      <h5 className='center'>    !</h5>
      <p style={{margin: '1.5rem initial'}} className="col s10 offset-s1 price-desc center">
         !       , 
        <br/>   '', ''      . 
        <br/>         ! 
      </p>
      <div className='row' id="basic-desc">
      <div className="input-field col s10 offset-s1">
        <h5 className='scorehvy'>#BASIC</h5>
        <div className="input-field col s6 range-selector">
          <label htmlFor="range_basic_price" className='active'>BAISC  </label>
          <Slider
            value={props.basic_price}
            onChange={props.handleRange('basic_price')}
            step={1}
            valueLabelDisplay='auto'
            valueLabelFormat={(value) => value + ''}
            aria-labelledby="range_basic_price"
            marks
            min={11}
            max={16} />
        </div>

        <div className="input-field col s3 offset-s3 price-tag">
          <input type="text" id='basic_price' value={props.basic_price + '0,000 '} disabled />
          <label htmlFor="basic_price" className='active'>BASIC </label>
        </div>
      </div>

      <div className="input-field col s10 offset-s1">
        <input className='has-character-counter _required' id='basic_intro' type="text" data-length='40' maxLength='41'
            placeholder='ex)      .' 
            onChange={props.handleChange}
            onKeyPress={props.handleKeyPress}
            value={props.basic_intro} />
        <label className='active' htmlFor="basic_intro">BASIC  </label>
        { props.need && !props.basic_intro ? <span className='red-text'><i className='material-icons'>error</i>  !</span> : null }   
      </div>

      <div className="input-field col s10 offset-s1">
        <label htmlFor="basic-options" className='active'>BASIC  ( 5)</label>
        <div className="chips chips-initial col s7 no-autoinit" id='basic-options'>
          <input type="text" id='basic_additional' className="custom-class" placeholder='' onKeyDown={props.handleChips} />
        </div>
      </div>

      <div className="input-field col s4 offset-s1">
        <select name="" id="basic_working" className='_required' value={props.basic_working} onChange={props.handleChange} >
          <option value="" disabled></option>
          <option value="1-3">1-3</option>
          <option value="3-5">3-5</option>
          <option value="5-7">5-7</option>
          <option value="7-10">7-10</option>
          <option value="10-15">10-15</option>
          <option value="15 ">15 </option>
        </select>
        <label htmlFor="basic_working">  </label>
        { props.need && !props.basic_working ? <span className='red-text'><i className='material-icons'>error</i>  !</span> : null }   
      </div>

      <div className="input-field col s4 offset-s2">
        <select name="" id="basic_modify" className='_required' value={props.basic_modify} onChange={props.handleChange}>
          <option value="" disabled> </option>
          <option value="1">1</option>
          <option value="2">2</option>
          <option value="3">3</option>
          <option value="4">4</option>
          <option value="5">5</option>
          <option value="6">6</option>
          <option value="7">7</option>
          <option value="8">8</option>
          <option value="9">9</option>
          <option value="10">10</option>
          <option value="15">15</option>
          <option value=""></option>
        </select>
        <label htmlFor="basic_modify">  </label>
        { props.need && !props.basic_modify ? <span className='red-text'><i className='material-icons'>error</i>  !</span> : null }   
      </div>

      <div className="input-field col s4 offset-s1">
        <select name="" id="basic_runningTime" className='_required' value={props.basic_runningTime} onChange={props.handleChange}>
          <option value="" disabled></option>
          <option value="1">1</option>
          <option value="2">2</option>
          <option value="3">3</option>
          <option value="4">4</option>
          <option value="5">5</option>
          <option value="6">6</option>
          <option value="7">7</option>
          <option value="8">8</option>
          <option value="9">9</option>
          <option value="10">10</option>
          <option value=""></option>
        </select>
        <label className='active' htmlFor="basic_runningTime"></label>
        { props.need && !props.basic_runningTime ? <span className='red-text'><i className='material-icons'>error</i>  !</span> : null }   
      </div>

      <div className="input-field col s4 offset-s2">
        <input onKeyDown={(e) => noWrite(e)} id='basic_additional_runningTime' value={props.basic_additional_runningTime} onChange={props.handleChange} placeholder='   ' type="number" min='0' step='5000' max='100000'/>
        <label className='active' htmlFor="basic_additional_runningTime">    ( 100,000)</label>
        <span className='guide-text right'>     ,     .</span>
      </div>

      </div>

      <div className="divider col s10 offset-s1"></div>

      {/* ----- PROFESSIONAL ----- */}
      <div className='row' id="pro-desc">
      <div className="input-field col s10 offset-s1">
        <h5 className='scorehvy'>#PROFESSIONAL</h5>
        <div className="input-field col s6 range-selector">
          <label htmlFor="range_pro_price" className='active'>PROFESSIONAL  </label>
          <Slider
            value={props.pro_price}
            onChange={props.handleRange('pro_price')}
            step={1}
            valueLabelDisplay='auto'
            valueLabelFormat={(value) => value + ''}
            aria-labelledby="range_pro_price"
            marks
            min={17}
            max={25} />
        </div>

        <div className="input-field col s3 offset-s3 price-tag">
          <input type="text" id='pro_price' value={props.pro_price + '0,000 '} disabled />
          <label htmlFor="pro_price" className='active'>PRO </label>
        </div>
      </div>

      <div className="input-field col s10 offset-s1">
        <input className='has-character-counter _required' id='pro_intro' type="text" data-length='40' maxLength='40'
            placeholder='ex)      .' 
            onChange={props.handleChange}
            onKeyPress={props.handleKeyPress}
            value={props.pro_intro} />
        <label className='active' htmlFor="pro_intro">PROFESSIONAL  </label>
        { props.need && !props.pro_intro ? <span className='red-text'><i className='material-icons'>error</i>  !</span> : null }   
      </div>

      <div className="input-field col s10 offset-s1">
        <label htmlFor="pro-options" className='active'>PROFESSIONAL  ( 7)</label>
        <div className="chips chips-initial col s7 no-autoinit" id='pro-options'>
          <input type="text" id='pro_additional' className="custom-class" placeholder='' onKeyDown={props.handleChips} />
        </div>
      </div>

      <div className="input-field col s4 offset-s1">
        <select name="" id="pro_working" className='_required' value={props.pro_working} onChange={props.handleChange} >
          <option value="" disabled></option>
          <option value="1-3">1-3</option>
          <option value="3-5">3-5</option>
          <option value="5-7">5-7</option>
          <option value="7-10">7-10</option>
          <option value="10-15">10-15</option>
          <option value="15 ">15 </option>
        </select>
        <label htmlFor="pro_working">  </label>
        { props.need && !props.pro_working ? <span className='red-text'><i className='material-icons'>error</i>  !</span> : null }
      </div>

      <div className="input-field col s4 offset-s2">
        <select name="" id="pro_modify" className='_required' value={props.pro_modify} onChange={props.handleChange}>
          <option value="" disabled> </option>
          <option value="1">1</option>
          <option value="2">2</option>
          <option value="3">3</option>
          <option value="4">4</option>
          <option value="5">5</option>
          <option value="6">6</option>
          <option value="7">7</option>
          <option value="8">8</option>
          <option value="9">9</option>
          <option value="10">10</option>
          <option value="15">15</option>
          <option value=""></option>
        </select>
        <label htmlFor="pro_modify">  </label>
        { props.need && !props.pro_modify ? <span className='red-text'><i className='material-icons'>error</i>  !</span> : null }
      </div>

      <div className="input-field col s4 offset-s1">
        <select name="" id="pro_runningTime" className='_required' value={props.pro_runningTime} onChange={props.handleChange}>
          <option value="" disabled></option>
          <option value="1">1</option>
          <option value="2">2</option>
          <option value="3">3</option>
          <option value="4">4</option>
          <option value="5">5</option>
          <option value="6">6</option>
          <option value="7">7</option>
          <option value="8">8</option>
          <option value="9">9</option>
          <option value="10">10</option>
          <option value=""></option>
        </select>
        <label className='active' htmlFor="pro_runningTime"></label>
        { props.need && !props.pro_runningTime ? <span className='red-text'><i className='material-icons'>error</i>  !</span> : null }   
      </div>

      <div className="input-field col s4 offset-s2">
        <input onKeyDown={(e) => noWrite(e)} id='pro_additional_runningTime' value={props.pro_additional_runningTime} onChange={props.handleChange} placeholder='   ' type="number" min='0' step='5000' max='100000'/>
        <label className='active' htmlFor="pro_additional_runningTime">    ( 100,000)</label>
        <span className='guide-text right'>     ,     .</span>
      </div>

      </div>

      <div className="divider col s10 offset-s1"></div>

      {/* ----- COMMON ----- */}
      {/* <div className='row' id="common-desc">
      <div className="input-field col s10 offset-s1">
        <h5 className="scorehvy"># <span className='guide-text'>(  .)</span></h5>
      </div>

      <div className="input-field col s10 offset-s1">
        <label id='personal-feeling-label' htmlFor="personal-feeling" className='active'>  ( 4)</label>
        <div className="chips chips-initial col s7 no-autoinit" id='personal-feeling'>
          <input type="text" id='personal-feeling-additional' className="custom-class" placeholder='ex) ' onKeyDown={props.handleChips} />
        </div>
      </div>
      </div> */}

    </Fragment>
  )
}

export default CreateServiceStep4;