import React, { Component, Fragment } from 'react';
import './purchase.css';
import M from 'materialize-css';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { firestoreConnect, isLoaded } from 'react-redux-firebase';
import { _buy_service } from '../../store/actions/serviceAction';
import { Redirect } from 'react-router-dom';

class Purchase extends Component {
  state = {
    selected : '',
    plus_time: 0,
  }
  componentDidUpdate(prevProps, prevState) {
    if(this.state.plus_time !== prevState.plus_time) {
      let price = this.props.location.price ? this.props.location.price : this.props.will_purchase.price[0];
      this.setState(prevState => ({
        ...prevState,
        total_price: this.numberWithCommas(parseInt((price.price).replace(/,/g,"")) + (price.additional_price * this.state.plus_time)),
      }))
    }
    if(this.state.selected !== prevState.selected) {
      const hidden_modal = document.querySelector('#modal-verify-payment');
      const radioBtn = [...document.querySelectorAll('.with-gap')];

      M.Modal.init(hidden_modal, {
        dismissible: false,
        preventScrolling: false,
        onCloseEnd: () => radioBtn.filter(item => {
          if(item.checked) {
            this.setState({
              selected: item.id,
            })
          }
        })
      })
    }
  }
	componentDidMount() {
    M.AutoInit();
  }
  numberWithCommas = (x) => {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  }
  handleSubmit = (e) => {
    e.preventDefault();
    const hidden_modal = document.querySelector('#modal-verify-payment');
    const modal_instance = M.Modal.getInstance(hidden_modal);
    modal_instance.open();
  }
  haddleKeyDown = (e) => {
    e.preventDefault();
  }
  handleChange = (e) => {
    if(e.target.id === 'plus_time') {
      this.setState({
        [e.target.id]: e.target.value,
      });
      return;
    }
    this.setState({
      selected: e.target.id,
    })
  }
  close_modal = (e) => {
    e.preventDefault();
    this.setState({
      selected: '',
    })
  }
  buy_by_click = (e, price) => {
    e.preventDefault();
    this.props._buy_service(this.props.match.params.service_id, this.props.will_purchase, price, this.state, this.props.history);
  }
	render(){
    const { auth } = this.props;
    if(auth.isLoaded && !auth.uid) {
      alert(' !');
      return <Redirect to='/signin' />
    } else if(auth.isLoaded && !auth.emailVerified) {
      alert('  !')
      return <Redirect to='/emailVerification' />
    }

    if(!isLoaded(this.props.will_purchase)) return <div className='container'>...</div>

    const { will_purchase } = this.props;
    const price = this.props.location.price ? this.props.location.price : will_purchase.price[0];
		return(
			<form onSubmit={this.handleSubmit} className="container purchase-page">
				<h5 className='col s6 scorehvy category'></h5>

        <div className="collection service-preview">
          <div className="collection-item row">
            <div className="col s3 image-area">
              <img height='140' className='preview-img' src={will_purchase.images.thumbnail} alt=" "/>
            </div>

            <div className="col s9 subContent">
              <h5 className="service-title scorehvy">{will_purchase.service_title}</h5>
              <p className='provider-nickname'>{will_purchase.provider_nickName} </p>
            </div>
          </div>
        </div>

				<div className="purchase-form">
					<table className="basic-purchase centered">
						<thead>
							<tr className=''>
                <th className='scorehvy'></th>
								<th className='scorehvy'></th>
								<th className='scorehvy'></th>
								<th className='scorehvy'></th>
							</tr>
						</thead>

						<tbody>
							<tr className=''>
                <td className="myomColor-background white-text"><h4 className="scorehvy">{price.type}</h4></td>
								<td className=''>
                  {
                    price.chips.length && price.chips.map(chip => (
                      <div className='options' key={chip}><i className="material-icons left">check</i><span className=''>{chip}</span></div>
                    ))
                  }
                </td>
								<td className=''>{ price.working }</td>
								<td className='scorehvy'>{ '' + price.price }</td>
							</tr>
						</tbody>
					</table>

          <h5 className='col s12 scorehvy category'> </h5>
					<table className="option-purchase centered">
						<thead>
							<tr>
								<th className='scorehvy'> </th>
								<th className='scorehvy'> </th>
								<th className='scorehvy'> </th>
							</tr>
						</thead>
						<tbody>
							<tr>
                <td>{price.runningTime}</td>
                <td><input onKeyDown={(e) => this.haddleKeyDown(e)} onChange={this.handleChange} value={this.state.plus_time} id='plus_time' type="number" min='0'/></td>
                <td>{'+ ' + this.numberWithCommas(price.additional_price * this.state.plus_time)}</td>
              </tr>
						</tbody>
					</table>
				</div>

        <h5 className='col s12 scorehvy category'></h5>
				<div className="purchase-price">
					<div className="panel-heading scorehvy"></div>
					<div className="panel-body">
						<div className="row">
							<div className="col s6">
                <div className="row">
                  <div className="col s6">
                    <strong></strong>
                  </div>
                  <span className="col s6">
                    <div data-target="modal-coupon" className="btn-small disable waves-effect waves-light indigo modal-trigger"></div>
                  
                    { /* Modal Structure */}
                    <div id="modal-coupon" className="modal">
                      <div className="modal-content">
                        <h5>  </h5>
                      </div>
                    </div>
                  </span>

                </div>

                <div className="row">
                  <div className="col s6"></div>
                  <div className="col s6 red-text">
                    - 0
                  </div>
                </div>
							</div>

							<div className="col s6">
                <div className="row">
								  <h5 style={{marginTop: 0}} className='col m6 s12 center'> </h5>
								  <h5 style={{marginTop: 0}} className="col m6 s12 center scorehvy">{ (this.state.total_price) ? '' + this.state.total_price : '' + price.price}</h5>
                </div>
							</div>

						</div>
					</div>
				</div>
				<div className="purchase-method">
					<div className="panel-heading scorehvy"> <font color='red'> (       !)</font></div>
					<div className="panel-body row">
						<p className='col s3'>
							<label htmlFor='pay_creditcard'>
								<input onChange={this.handleChange} id='pay_creditcard' name="method" className="with-gap" type="radio" disabled required />
								<span></span>
							</label>
						</p>
						<p className='col s3'>
							<label htmlFor='pay_kontobezahlen'>
								<input onChange={this.handleChange} id='pay_kontobezahlen' name="method" className="with-gap" type="radio" required/>
								<span></span>
							</label>
						</p>
						<p className='col s3'>
							<label htmlFor='pay_bankaccount'>
								<input onChange={this.handleChange} id='pay_bankaccount' name="method" className="with-gap" type="radio" disabled required/>
								<span></span>
							</label>
						</p>
						<p className='col s3'>
							<label htmlFor='pay_cellphone'>
								<input onChange={this.handleChange} id='pay_cellphone' name="method" className="with-gap" type="radio" disabled required/>
								<span></span>
							</label>
						</p>
					</div>
				</div>
				<div className="tax">
					<div className="panel-heading scorehvy"> </div>
					<div className="panel-body">
						<li> myom     .</li>
						<li>   []  .</li>
						<li>[]   ,       .</li>
						<li>[]      ,  7   ,         .</li>
						<li>        .</li>
						<li>myom   ,    !</li>
					</div>
				</div>


			  <button className="btn-large waves-effect waves-light indigo right purchase-btn"></button>   


        {
          (this.state.selected)
            ? (
              <Fragment>
              <input type='hidden' id='hidden_btn_for_additional_modal' data-target='modal-verify-payment' className="btn-large modal-trigger btn waves-effect" />
              <div id="modal-verify-payment" className="modal no-autoinit modal-fixed-footer">
                <div className="modal-content">
                  {/* <h5 className='scorehvy'>{this.state.selected === 'pay_creditcard' ? ' '
                        : this.state.selected === 'pay_kontobezahlen' ? ' '
                        : this.state.selected === 'pay_bankaccount' ? ' '
                        : this.state.selected === 'pay_cellphone' ? ' ' : ''}</h5> */}
                  <p style={{marginBottom: '3rem'}} className='grey-text center'>           .</p>
                  
                  <h5 className="scorehvy"> <span style={{fontFamily: 'sans-serif', fontSize: '15px'}} className='right'></span></h5>
                  <p style={{marginBottom: '3rem'}}>

                  <strong style={{fontWeight: 'bolder'}}>1)  </strong><br/>  
                  1.        ,       .<br/>
                  2.    []  []  ,      []   .<br/>
                  3.    Time Stamp      .<br/>
                  4.        , 100%  .        .
                  <br/><br/>
                  <strong style={{fontWeight: 'bolder'}}>2)  </strong><br/>
                  1.       ,      .<br/>
                  2.    []  []  ,      []   .<br/> 
                  3.        100%  .

                  </p>

                  <h5 className="scorehvy"> </h5>
                  <div className="row">
                    <p style={{paddingLeft: 0}} className="col s4"></p>
                    <p className="col s8">{price.type}</p>
                    <div className="divider col s12"></div>
                    <p style={{paddingLeft: 0}} className="col s4"></p>
                    <p className="col s8">{price.chips.length && price.chips.map(chip => chip + ' / ')}</p>
                    <div className="divider col s12"></div>
                    <p style={{paddingLeft: 0}} className="col s4"></p>
                    <p className="col s8">{this.state.plus_time ? this.state.plus_time + '' : ''}</p>
                    <div className="divider col s12"></div>
                    <p style={{paddingLeft: 0}} className="col s4"></p>
                    <p className="col s8">{this.state.plus_time ? this.numberWithCommas(price.additional_price * this.state.plus_time) + '' : ' '}</p>
                    <div className="divider col s12"></div>
                  </div>

                  <h5 style={{marginTop: '3rem'}} className="scorehvy center">  : {(this.state.total_price) ? '' + this.state.total_price : '' + price.price}</h5>
                </div>
                <div className="modal-footer">
                  <button onClick={this.close_modal} className="modal-close btn-flat waves-effect left"></button>  
                  <button onClick={(e) => this.buy_by_click(e, price)} className="btn-flat waves-effect right"></button>  
                </div>              
              </div>
              </Fragment>
            )
            : null
        }
			</form>
		)
	}
}
const mapStateToProps = (state) => {
  return {
    will_purchase: state.firestore.data.will_purchase,
    auth: state.firebase.auth,
  }
}
const mapDisptachToProps = (dispatch) => {
  return {
    _buy_service: (service_id, will_purchase, price, plus, history) => dispatch(_buy_service(service_id, will_purchase, price, plus, history)),
  }
}
export default compose(
  connect(mapStateToProps, mapDisptachToProps),
  firestoreConnect((props) => [
    { collection: 'testService', doc: props.match.params.service_id, storeAs: 'will_purchase' }
  ])
)(Purchase);


