import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './theme.css';

class Theme extends Component {
  componentDidMount() {

  }
  render() {
    return (
      <div className="container theme">
        <div className="row categories">
          <h5 className="col s12 scorehvy myomColor">MYOM </h5>
          <Link to='thema/cinema'>
            <div className="col s6">
              <div className="card">
                <div className="card-image">
                  <img src="/img/categories/.jpg" alt="  "/>
                  <span className='scorehvy'></span>
                </div>
              </div>
            </div>
          </Link>

          <Link to='thema/variety'>
            <div className="col s3">
              <div className="card">
                <div className="card-image">
                  <img src="/img/categories/2.jpg" alt="  "/>
                  <span className='scorehvy'></span>
                </div>
              </div>
            </div>
          </Link>

          <Link to='thema/document'>
            <div className="col s3">
              <div className="card">
                <div className="card-image">
                  <img src="/img/categories/.jpg" alt="  "/>
                  <span className='scorehvy'></span>
                </div>
              </div>
            </div>
          </Link>

          <Link to='thema/dynamic'>
            <div className="col s3">
              <div className="card">
                <div className="card-image">
                  <img src="/img/categories/.PNG" alt="  "/>
                  <span className='scorehvy'></span>
                </div>
              </div>
            </div>
          </Link>

          <Link to='thema/vlog'>
            <div className="col s6">
              <div className="card">
                <div className="card-image">
                  <img src="/img/categories/.png" alt="  "/>
                  <span className='scorehvy'></span>
                </div>
              </div>
            </div>
          </Link>
          
          <Link to='thema/etc'>
            <div className="col s3">
              <div className="card">
                <div className="card-image">
                  <img src="/img/categories/.jpg" alt="  "/>
                  <span className='scorehvy'></span>
                </div>
              </div>
            </div>
          </Link>

          {/* { themas && themas.map(item => {
            return (
              <Link to={'/thema/' + item.title} key={item.src} >
                <ThemeSummary thema={item} />
              </Link>
            )
          })} */}

        </div>
      </div>
    )
  }
}


export default Theme;