import React, { Component } from 'react'
import { connect } from 'react-redux'
import { withdrawal } from '../../store/actions/authAction'
import { Redirect } from 'react-router-dom'
import './profile.css'

class Withdrawal extends Component {
	state = {
		email: ''
	}
	handleChange= (e) => {
		this.setState({
			[e.target.id] : e.target.value
		});
	}
	handleSubmit = (e) => {
    e.preventDefault();
    if(window.confirm('  ?    .')) {
      this.props.withdrawal(this.state);
    } else {
      return;
    }
	}
	render(){
  const { auth } = this.props;

	if(!auth.uid) return <Redirect to ='/' />
	return(
		<div className="profile_deatails">
			<div className="row">
				<h5 className="col s12 scorehvy sub-title"></h5>
				<div className="col s12">
					<div className="withdrawal white">
						<h6 className=''></h6>
						<div className="divider"></div>
						<div className="row">
							<form onSubmit = {this.handleSubmit}>
                <div className="input-field col s12">
                  <p>
                    <label>
                      <input className="with-gap" type="radio" name="withdrawal-menu" required/>
                      <span> </span>
                    </label>
                  </p>
                  <p>
                    <label>
                      <input className="with-gap" type="radio" name="withdrawal-menu" required/>
                      <span>   </span>
                    </label>
                  </p>
                  <p>
                    <label>
                      <input className="with-gap" type="radio" name="withdrawal-menu" required/>
                      <span>   </span>
                    </label>
                  </p>
                  <p>
                    <label>
                      <input className="with-gap" type="radio" name="withdrawal-menu" required/>
                      <span>  </span>
                    </label>
                  </p>
                  <p>
                    <label>
                      <input className="with-gap" type="radio" name="withdrawal-menu" required/>
                      <span></span>
                    </label>
                  </p>
                </div>
                
                <div className="col s12">
                  <h6 className=''> </h6>
                  <div style={{marginBottom: '1rem'}} className="divider"></div>
                </div>

							  <div className="input-field col s12">
                  <label className='active' htmlFor="email"> </label>
									<input type="text" placeholder='  ' className="validate" id="email" onChange= { this.handleChange } required/>
                  <p style={{fontSize: '12px'}} className="guide-text red-text">     .</p>

									<button className="btn myomColor-background right"></button>

							  </div>
              </form>
						</div>
					</div>
				</div>
			</div>
		</div>
	)
}
}

const mapStateToProps = (state) => {
	return {
		auth: state.firebase.auth
	}
}

const mapDispatchToProps = (dispatch) => {
	return {
		withdrawal: (user) => dispatch(withdrawal(user))
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(Withdrawal);